import { Injectable } from '@angular/core';
import { HttpClient} from '@angular/common/http';
@Injectable({
  providedIn: 'root'
})
export class EnrollmentService {
  
  _url = 'https://edtech-angular-server-git-kubernetesml.apps.us-east-1.starter.openshift-online.com/dashboard';
  _url1 = 'https://edtech-angular-server-git-kubernetesml.apps.us-east-1.starter.openshift-online.com/logout';
  constructor(private _http: HttpClient) { }
  
  enroll()
  {
   
    return this._http.get<any>(this._url,{withCredentials: true});
  }
  signout(){
    return this._http.get<any>(this._url1,{withCredentials: true});
  }


}
