import { Component, OnInit,Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
@Component({
  selector: 'app-del-program',
  templateUrl: './del-program.component.html',
  styleUrls: ['./del-program.component.css']
})
export class DelProgramComponent implements OnInit {
  eno;pno;
  delProgram = false;
  constructor(public dialogRef: MatDialogRef<DelProgramComponent>, @Inject(MAT_DIALOG_DATA) data) { this.eno = data.eno; this.pno = data.pno }
  
  ngOnInit() {
  }
  delete() {
    this.delProgram = true
    this.dialogRef.close(this.delProgram);
  }

close() {
    this.dialogRef.close();
}

}
