import { Injectable } from '@angular/core';
import { HttpClient} from '@angular/common/http';
@Injectable({
  providedIn: 'root'
})
export class LoginService {

  _url = 'https://edtech-angular-server-git-kubernetesml.apps.us-east-1.starter.openshift-online.com/login';
  _url1 = 'https://edtech-angular-server-git-kubernetesml.apps.us-east-1.starter.openshift-online.com/forgotpassword';
_url10 ='https://edtech-angular-server-git-kubernetesml.apps.us-east-1.starter.openshift-online.com/logout'
  constructor(private _http: HttpClient) { }
  
  enroll(user)
  {
    console.log(user);
    return this._http.post<any>(this._url,user,{withCredentials: true});
  }

  enroll1(user){
    return this._http.post<any>(this._url1,user);
  }

  signout()
  {
    return this._http.get<any>(this._url10,{withCredentials: true});

  }
}
