import { Injectable } from '@angular/core';
import { HttpClient} from '@angular/common/http';
@Injectable({ 
  providedIn: 'root'
})
export class cwebinarService {
  
  _url3=  'http://localhost:3000/dashboard';

 _url1 ='http://localhost:3000/webinar'
 _url2 ='http://localhost:3000/getwebinardata'



  constructor(private _http: HttpClient) { }


  enroll3()
  {
   
    return this._http.get<any>(this._url3,{withCredentials: true});
  }

  enroll2()
  {
   
    return this._http.get<any>(this._url2,{withCredentials: true});
  }

  enroll(user)
  {
   
    return this._http.post<any>(this._url1,user);
  }

 


}
