import { Injectable } from '@angular/core';
import { HttpClient} from '@angular/common/http';
@Injectable({
  providedIn: 'root'
})
export class QuizService {
  _url = 'https://edtech-angular-server-git-kubernetesml.apps.us-east-1.starter.openshift-online.com/getQuiz';
  constructor(private _http: HttpClient) { }
  getQuiz()
  {
    return this._http.post<any>(this._url,{withCredentials: true});
  }

}
