    this.delPeno = this.manual[i].ind+1;
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = {
      eno:this.delPeno,
      pno: this.delPpno,

    };
    this.matDialog.open(DelProgramComponent, dialogConfig);
    const dialogRef = this.matDialog.open(DelProgramComponent, dialogConfig);
    dialogRef.afterClosed().subscribe(
      data => {console.log("Dialog output:", data);
      if(data == true){
        this.deleteAt(this.manual[i].topic,j);
      }
    }
  );
