  insertAt1(array, index, elements) {
    if(array == null){
      array.push(elements)
      console.log(elements);
    }
    else{
      array.splice(index, 0, elements);
    }
    // array.splice(index, 0, elements);
    for (let i = 0; i < array.length; i++) {
      this.manual[this.quiznum-1].topic[i].tind = i;
      
    }
  }
  insertAt(array, index, elements) {
    if(array == null){
      array[0] = elements;
      console.log(elements);
    }
    else{
      array.splice(index, 0, elements);
    }
    
    for (let i = 0; i < array.length; i++) {
      this.manual[i].ind = i;
      
    }

  }
