import { Injectable } from '@angular/core';
import { HttpClient} from '@angular/common/http';
@Injectable({ 
  providedIn: 'root'
})
export class cwebinarService1 {
  
  _url3=  'https://edtech-angular-server-git-kubernetesml.apps.us-east-1.starter.openshift-online.com/dashboard';

 _url1 ='https://edtech-angular-server-git-kubernetesml.apps.us-east-1.starter.openshift-online.com/webinar'
 _url2 ='https://edtech-angular-server-git-kubernetesml.apps.us-east-1.starter.openshift-online.com/getwebinardata'



  constructor(private _http: HttpClient) { }


  enroll3()
  {
   
    return this._http.get<any>(this._url3,{withCredentials: true});
  }

  enroll2()
  {
   
    return this._http.get<any>(this._url2,{withCredentials: true});
  }

  enroll(user)
  {
   
    return this._http.post<any>(this._url1,user);
  }

 


}
